import React from "react";
import { Alert, Container } from "react-bootstrap";
import Motivation from './motivation';
import Cv from "./cv"

const Home = () => {
  return (
    <Container fluid className="m-0 p-0 ">
      <div className="w-100 d-flex justify-content-center">

        <Alert variant="danger" className="col-10">
          <h1>Uwaga!</h1>
          <h4>Wszelkie rozwiązania zastosowane przeze mnie pochodzą z początków mojej nauki, proszę pamiętaj o tym przed subiektywną oceną mojej pracy ;)</h4>
          <p>Mimo wszystko uznałem że to część mojej historii i nie chcę za bardzo ingerować w ten kod, lepiej towrzyć następny</p>
        </Alert>
      </div>
      <Motivation />
      <Cv />
    </Container>
  )
}
export default Home;